@import '../../scss/colors';

.wrapperToDoList {

  .wrapperPostList {
    width: 100%;
    max-height: 100%;
    margin-top: 2rem;


    .postListTitle {
      margin-top: 2rem;
      text-align: center;
      color: $color-main-default;
    }

  }

  .emptyList {
    display: flex;
    margin-top: 2rem;
    justify-content: center;
    flex-wrap: wrap;

    h2 {
      color: $color-main-default;
      font-size: 2.4rem;
      margin-top: 2rem;
      width: 100%;
      text-align: center;
    }

        .emptyImg {
      width: 400px;
      height: 400px;
      border-radius: 50%;
      margin-top: 2rem;
      overflow: hidden;
      box-shadow: 0 0 20px $color-main-default;
      display: flex;
      justify-content: center;
      animation: 60s linear infinite rotate;

      img{
        height: 100%;
      }

      @keyframes rotate {
        from {
          transform: rotate(0deg);
        }
        to {
          transform: rotate(360deg);
        }
      }

    }
  }
}