@import "../../scss/colors";

.myInput {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
  align-items: center;
  margin: 0 auto;

  input {
    width: 100%;
    height: 100%;
    padding-left: 1rem ;

    background-color: inherit;

    outline: none;
    border: 2px solid $color-main-default;
    border-radius: 0.6rem;

    &:focus {
      border: 2px solid $color-main-secondary;
    }
  }
  .button {

  }
}

@media screen and (max-width: 768px) {
  .myInput {
    padding: 3px;

    input {
      border-radius: 0.4rem;
    }
  }
}

